.contacts {
  padding-top: 60px;
  padding-bottom: 47px;
  width: 50%;

  @media (max-width: $vp-1199) {
    padding-top: 69px;
    padding-bottom: 60px;
    width: 100%;
  }

  @media (max-width: $vp-767) {
    padding-bottom: 61px;
  }
}

.contacts__wrapper {
  margin: 0 auto;
  padding-left: 87px;
  max-width: 1160px;
  display: flex;
  flex-direction: column;

  @media (max-width: $vp-1199) {
    padding-left: 83px;
    max-width: 678px;
    flex-direction: column;
  }

  @media (max-width: $vp-767) {
    flex-direction: column;
    margin-left: 0;
    padding-left: 0;
    max-width: 100%;
  }
}

.contacts__wrapper h2 {
  margin: 0;
  margin-bottom: 34px;
  font-family: $ff-bebas-neue;
  font-size: 49px;
  line-height: 64px;
  letter-spacing: 0.03em;

  @media (max-width: $vp-1199) {
    margin-left: 3px;
    font-size: 42px;
    line-height: 42px;
  }

  @media (max-width: $vp-767) {
    margin-bottom: 36px;
    margin-left: 0;
    font-size: 32px;
    line-height: 32px;
  }
}

.contacts__description-wrapper {
  padding-left: 120px;
  width: 50%;
}

.contacts__description-columns {
  position: relative;
  display: flex;
  padding-left: 5px;

  &::after {
    content: "";
    position: absolute;
    top: 10px;
    bottom: 26px;
    left: 51%;
    width: 1px;
    background-color: $color-light-slate-grey;

    @media (max-width: $vp-1199) {
      top: 10px;
      bottom: 26px;
      left: 46%;
    }

    @media (max-width: $vp-767) {
     width: 0;
    }
  }

  @media (max-width: $vp-767) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 0;
  }
}

.contacts__description-columns h3 {
  margin: 0;
  margin-bottom: 3px;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;

  @media (max-width: $vp-1199) {
    margin-bottom: 5px;
    font-size: 20px;
    line-height: 29px;
  }
}

.contacts__description-columns p {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;

  @media (max-width: $vp-1199) {
    font-size: 18px;
    line-height: 30px;
  }
}

.contacts__description-col1 {
  width: 230px;

  @media (max-width: $vp-1199) {
    width: 263px;
  }

  @media (max-width: $vp-767) {
    margin-right: 20px;
    margin-bottom: 19px;
  }
}

.contacts__description-col2 {
  padding-left: 60px;
  width: 226px;

  @media (max-width: $vp-1199) {
    padding-left: 50px;
  }

  @media (max-width: $vp-767) {
    width: 142px;
    padding-left: 0;
  }
}

.contacts__address {
  margin-bottom: 18px;
}

.contacts__schedule {
  margin-bottom: 18px;
}

.contacts__phone {
  margin-bottom: 14px;

  @media (max-width: $vp-1199) {
    margin-bottom: 22px;
  }
}

.contacts__email {
  margin-bottom: 14px;
}

.contacts__phone a,
.contacts__email a {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;

  @media (max-width: $vp-1199) {
    font-size: 18px;
    line-height: 27px;
  }
}
